import React from 'react';
import '../css/Subpage.scss';
import { franchise_sub } from '../component/NavData';
import { Link } from 'react-router-dom';
import '../css/Franchise.scss';

const Franchise03 = ({ content }) => {
    const num = 3;
    return (
        <div className='Subpage'>
            <div className="subImg subImg04_3"></div>
            <div className="navBar">
                <div className="inner">
                    <div className="title">
                        {content[num].title}
                    </div>
                    <ul className="list">
                        <li><Link to="/franchise">{franchise_sub[0].title}</Link></li>
                        <li><Link to="/franchise/sub02">{franchise_sub[1].title}</Link></li>
                        <li style={{ fontWeight: 700 }}><Link to="/franchise/sub03">{franchise_sub[2].title}</Link></li>
                    </ul>
                </div>
            </div>
            <div className='container'>
                <h2></h2>
                <div className="form">
                    <div className='ftit'> </div>
                    <div><p></p><input type="text" /></div>
                    <div><p></p><input type="text" /></div>
                    <div><p></p><input type="text" /></div>
                    <div><p></p>
                        <label for="yes"></label>
                        <input id="yes" type="radio" value="" name="yn" />&nbsp;
                        <label for="no"></label>
                        <input id="no" type="radio" checked value="" name="yn" />
                    </div>
                    <div><p></p>
                        <label for="yes"></label>
                        <input id="yes" type="radio" value="" name="yn" />&nbsp;
                        <label for="no"></label>
                        <input id="no" type="radio" checked value="" name="yn" />
                    </div>
                    <div><p> </p>
                        <label for="yes"></label>
                        <input id="yes" type="radio" value="" name="yn" />&nbsp;
                        <label for="no"></label>
                        <input id="no" type="radio" checked value="" name="yn" />
                    </div>
                    <div><p></p><input type="text" /></div>
                    <div className='ftit'>  </div>
                    <div><p></p><input type="text" /></div>
                    <div><p></p><textarea name="mb_msg" rows="5" cols="24"></textarea> </div>
                </div>
            </div>
        </div>
    )
}

export default Franchise03;