import React from 'react'
import '../css/Community.scss'

const NEWS = [
  { id: 1, title: "     ", content: "", date: "2022-09-20", link: "http://www.shinailbo.co.kr/news/articleView.html?idxno=1600524" },
  { id: 2, title: "   2000  ", content: "", date: "2022-09-17", link: "https://www.mk.co.kr/premium/behind-story/view/2022/09/32469/" },
  { id: 3, title: ",     ", content: "CBC News", date: "2022-08-31", link: "http://www.cbci.co.kr/news/articleView.html?idxno=432117" },
  { id: 4, title: ",    ", content: "", date: "2022-08-23", link: "http://www.wsobi.com/news/articleView.html?idxno=172391" },
  { id: 5, title: "     HMR  ...", content: "", date: "2022-08-05", link: "http://digitalchosun.dizzo.com/site/data/html_dir/2022/08/05/2022080580192.html" },
  { id: 6, title: ",    ", content: "", date: "2022-08-05", link: "https://view.asiae.co.kr/article/2022080509070997201" },
]

const Community01 = () => {
  return (
    <div className='container'>
      <h2>NEWS</h2>
      <ul className='com'>
        {
          NEWS.map(news => {
            return (
              <li key={news.id}>
                <a href={news.link} target="blank_">
                  <strong>{news.title}</strong>
                  <p>{news.content}</p>
                  <span>{news.date}</span>
                </a>
              </li>
            )
          })
        }
      </ul>
    </div>
  )
}

export default Community01;