import React from 'react'
import '../css/Notice.scss'

const NOTICE = [
  { id: 1, title: "  ", content: "    ", date: "2022-09-19" },
  { id: 2, title: " - :       !", content: "9 19 10 31  -  !", date: "2022-09-01" },
  { id: 3, title: "2022     ", content: "            .", date: "2022-09-01" },
  { id: 4, title: ", 2022  100  !", content: "      .", date: "2022-07-13" },
  { id: 5, title: " D.I.Y  !!!", content: "    .", date: "2022-07-01" },
  { id: 6, title: " 7  -   ", content: " 77,000 !", date: "2022-06-08" },
]

const Notice01 = () => {
  return (
    <div className='container'>
      <h2>NOTICE</h2>
      <ul className='ntc'>
        {
          NOTICE.map(ntc =>{
            return (
              <li key={ntc.id}>
                <strong>{ntc.title}</strong>
                <p>{ntc.content}</p>
                <span>{ntc.date}</span>
              </li>
            )
          })
        }
      </ul>
    </div>
  )
}

export default Notice01;